<template lang="pug">
    div.extract_record-conent
        template(v-if="isLoaded")
            div.extract-count(v-text="`累计领取:${money.total_money}元`") 
            template(v-if="list && list.length > 0")
                ul.extract-list
                    li.extract-item(v-for="item in list")
                        div.item-left(v-text="`${item.money}元`") 
                        div.item-right
                            p.time(v-text="item.create_time")
                            p.state 兑换中
            template(v-else)
                null-data
</template>
<script>
import { Toast } from 'mint-ui';
import nullData from '@/components/nullData'
export default {
    name: 'profitExtractRecord',
    components: {
        nullData
    },
    data() {
        return {
            money: '',
            list: '',
            isLoaded: false
        }
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/user_extract_record' : this.api.userExtractRecord();
            this.axios.get(axiosUrl).then(res => {
                this.isLoaded = true;
                if (res.data.status == 1) {
                    this.money = res.data;
                    this.list = res.data.list;
                }
                else {
                    Toast(res.data.error || '请求出错')
                }

            })
            .catch(err => {
               Toast(err || '网络异常，请求失败')
            })
        }

    }
}
</script>

<style lang="less" scoped>
@import '../../../assets/css/variable.less';
.extract_record-conent {
    .extract-count {
        height: 1.95rem;
        width: 100%;
        text-align: center;
        line-height: 1.95rem;
        color: @fc-light;
        background: #f7f7f7;
    }
    .extract-item {
        height: 2.5rem;
        padding: 0 .5rem;
        width: 100%;
        background: #fff;
        border-bottom: 1px solid #e6e6e6;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .item-left {
            color: @fc-main;
        }
        .item-right {
            color: @fc-light;
            text-align: right;
            font-size: .6rem;
            .time {
                font-size: .55rem;
            }
        }
    }
}
</style>


